.wrapper{width:100%;height:100%;display:flex;align-items:center;justify-content:center}.product{max-width:400px;width:100%;background:#fff;padding:10px;border-radius:10px}.product__content{min-height:300px}.product__action{display:flex;gap:10px;flex-wrap:wrap;padding:20px 0 10px;border-top:1px solid rgba(51,51,51,.2)}.content__list{max-height:360px;overflow-y:auto;list-style:none}.content__list::-webkit-scrollbar{width:6px;background:#f7f7f7}.content__list::-webkit-scrollbar-thumb{background-color:rgba(112,198,255,.4117647059);border-radius:9em;box-shadow:inset 1px 1px 10px rgba(112,198,255,.4117647059)}.content__list::-webkit-scrollbar-thumb:hover{background-color:rgba(104,187,255,.4784313725)}.content__item{position:relative;display:flex;padding:10px;gap:15px}.content__item:not(:last-child){border-bottom:1px dashed #5e5e5e}.content__item input{display:none}.content__item input:checked~.is-delete::after{width:100%}.content__item input:checked~label{background:#4193ff}.content__item input:checked~label::before{content:"";position:absolute;top:0;left:0;color:#fff;pointer-events:none}.content__item input:checked~label::after{content:"";display:block;position:absolute;top:10px;left:16px;width:6px;height:14px;border:solid #fff;border-width:0 2px 2px 0;transform:rotate(45deg);pointer-events:none}.content__item p{flex:1;font-size:18px}.content__text{display:flex;align-items:center;width:20px;height:20px;border:1px solid rgba(51,51,51,.2);border-radius:3px;font-size:20px;color:#5e5e5e;cursor:pointer;transition:.3s}.content__text:hover{border-color:#4193ff}.content__text i{margin-right:10px;cursor:pointer}.action__input{width:100%;min-height:40px;border:1px solid #c6c6c6;border-radius:10px;overflow:hidden}.action__input input{width:100%;height:100%;outline:none;padding:0 10px;border:none;font-family:"Fira Code",sans-serif;font-size:16px;color:#333}.button{width:100%;padding:10px 20px;background:#4193ff;font-size:18px;font-weight:500;color:#fff;border:none;border-radius:10px;cursor:pointer;transition:.3s}.button:hover{background:#2d7de6}.button:disabled{opacity:.6;pointer-events:none}.is-delete{position:relative}.is-delete::after{content:"";width:0%;height:3px;position:absolute;top:calc(50% - 1.5px);left:0;background:tomato;transition:300ms ease-in-out}.material-icons{color:rgba(51,51,51,.2);transition:.3s;cursor:pointer}.material-icons:hover{color:tomato}